import React, { createContext, useContext, useState, useEffect } from 'react';

const UserContext = createContext();

export const useUser = () => {
  const context = useContext(UserContext);
  if (!context) {
    throw new Error('useUser must be used within a UserProvider');
  }
  return context;
};

export const UserProvider = ({ children }) => {
  const initialLang = typeof window !== 'undefined' ? (localStorage.getItem('lang') || 'hindi') : 'hindi';

  const [user, setUser] = useState({
    name: ' ',
    phone: '+91 98765 43210',
    location: ' , ',
    farmSize: '5 ',
    language: initialLang,
    isLoggedIn: false,
    loginTime: null
  });

  const [language, setLanguage] = useState(initialLang);

  // Centralized translations for multi-language support
  const translations = {
    hindi: {
      appName: '',
      appSubtitle: '  ',
      welcome: '',
      online: '',
      logout: '',
      back: ' ',
      season: '',
      duration: '',
      water: '',
      temp: '',
      yield: '',
      care: '  ',
      economics: ' ',
      marketPrice: ' ',
      investment: '',
      expectedProfit: ' ',
      chooseCrop: '   ',
      viewOthers: '  ',
      recentActivity: '  ',
      farmSize: '  ',
      currentSeason: ' ',
      successRate: ' ',
    },
    english: {
      appName: 'CropAI',
      appSubtitle: 'Smart Farming Platform',
      welcome: 'Hello',
      online: 'Online',
      logout: 'Logout',
      back: 'Back',
      season: 'Season',
      duration: 'Duration',
      water: 'Water',
      temp: 'Temperature',
      yield: 'Yield',
      care: 'Care Instructions',
      economics: 'Economic Information',
      marketPrice: 'Market Price',
      investment: 'Investment',
      expectedProfit: 'Estimated Profit',
      chooseCrop: 'Choose this crop',
      viewOthers: 'View other options',
      recentActivity: 'Recent Activity',
      farmSize: 'Farm Size',
      currentSeason: 'Current Season',
      successRate: 'Success Rate',
    },
    marathi: {
      appName: '',
      appSubtitle: '  ',
      welcome: '',
      online: '',
      logout: '',
      back: ' ',
      season: '',
      duration: '',
      water: '',
      temp: '',
      yield: '',
      care: ' ',
      economics: ' ',
      marketPrice: ' ',
      investment: '',
      expectedProfit: ' ',
      chooseCrop: '  ',
      viewOthers: '  ',
      recentActivity: ' ',
      farmSize: ' ',
      currentSeason: ' ',
      successRate: ' ',
    },
    gujarati: {
      appName: '',
      appSubtitle: '  ',
      welcome: '',
      online: '',
      logout: '',
      back: ' ',
      season: '',
      duration: '',
      water: '',
      temp: '',
      yield: '',
      care: ' ',
      economics: ' ',
      marketPrice: ' ',
      investment: '',
      expectedProfit: ' ',
      chooseCrop: '   ',
      viewOthers: '  ',
      recentActivity: ' ',
      farmSize: ' ',
      currentSeason: ' ',
      successRate: ' ',
    }
  };

  const [recommendations, setRecommendations] = useState([]);
  const [alerts, setAlerts] = useState([
    {
      id: 1,
      title: ' ',
      titleEn: 'Weather Alert',
      titleMr: ' ',
      titleGu: ' ',
      message: '      ',
      messageEn: 'Rain expected this evening',
      messageMr: '     ',
      messageGu: '    ',
      priority: 'high',
      timestamp: new Date().toISOString()
    },
    {
      id: 2,
      title: ' ',
      titleEn: 'Pest Management',
      titleMr: ' ',
      titleGu: ' ',
      message: '    ',
      messageEn: 'Pest threat in wheat crops',
      messageMr: '   ',
      messageGu: '   ',
      priority: 'medium',
      timestamp: new Date().toISOString()
    }
  ]);

  const updateUser = (newUserData) => {
    setUser(prev => ({ ...prev, ...newUserData }));
  };

  const loginUser = (userData) => {
    setUser(prev => ({ 
      ...prev, 
      ...userData, 
      isLoggedIn: true, 
      loginTime: new Date().toISOString() 
    }));
  };

  const logoutUser = () => {
    setUser(prev => ({ 
      ...prev, 
      isLoggedIn: false, 
      loginTime: null 
    }));
  };

  const addRecommendation = (recommendation) => {
    setRecommendations(prev => [recommendation, ...prev]);
  };

  const addAlert = (alert) => {
    setAlerts(prev => [alert, ...prev]);
  };

  const changeLanguage = (newLanguage) => {
    setLanguage(newLanguage);
    setUser(prev => ({ ...prev, language: newLanguage }));
  };

  useEffect(() => {
    try {
      localStorage.setItem('lang', language);
    } catch (_) {}
  }, [language]);

  // Current language bundle and available languages list
  const t = translations[language] || translations.hindi;
  const languages = [
    { code: 'hindi', name: '', flag: '' },
    { code: 'english', name: 'English', flag: '' },
    { code: 'marathi', name: '', flag: '' },
    { code: 'gujarati', name: '', flag: '' }
  ];

  const value = {
    user,
    updateUser,
    loginUser,
    logoutUser,
    recommendations,
    addRecommendation,
    alerts,
    addAlert,
    language,
    changeLanguage,
    translations,
    t,
    languages
  };

  return (
    <UserContext.Provider value={value}>
      {children}
    </UserContext.Provider>
  );
};
