import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useUser } from '../context/UserContext';
import LanguageSwitcher from './LanguageSwitcher';
import { 
  ArrowLeft, 
  MapPin, 
  Ruler, 
  Droplets, 
  Wheat, 
  Zap,
  Smartphone,
  Globe,
  Calendar
} from 'lucide-react';

const CropRecommendationInput = () => {
  const navigate = useNavigate();
  const { language, t } = useUser();
  const [formData, setFormData] = useState({
    farmSize: '',
    soilType: '',
    location: '',
    previousCrop: '',
    season: '',
    irrigation: '',
    budget: ''
  });

  const [isLoading, setIsLoading] = useState(false);

  // Multilingual dropdown options
  const soilTypeOptions = {
    hindi: [
      { value: 'clay', label: '  (Clay)', description: '    ' },
      { value: 'loamy', label: '  (Loamy)', description: '    ' },
      { value: 'sandy', label: '  (Sandy)', description: '    ' },
      { value: 'silt', label: '  (Silt)', description: '  ' },
      { value: 'red', label: '  (Red Soil)', description: '   ' }
    ],
    english: [
      { value: 'clay', label: 'Clay Soil', description: 'Heavy and water-retaining' },
      { value: 'loamy', label: 'Loamy Soil', description: 'Best for crop production' },
      { value: 'sandy', label: 'Sandy Soil', description: 'Light and well-draining' },
      { value: 'silt', label: 'Silt Soil', description: 'Medium texture' },
      { value: 'red', label: 'Red Soil', description: 'High iron content' }
    ],
    marathi: [
      { value: 'clay', label: '  (Clay)', description: '    ' },
      { value: 'loamy', label: '  (Loamy)', description: '  ' },
      { value: 'sandy', label: '  (Sandy)', description: '    ' },
      { value: 'silt', label: '  (Silt)', description: ' ' },
      { value: 'red', label: '  (Red Soil)', description: '  ' }
    ],
    gujarati: [
      { value: 'clay', label: '  (Clay)', description: '   ' },
      { value: 'loamy', label: '  (Loamy)', description: '   ' },
      { value: 'sandy', label: '  (Sandy)', description: '   ' },
      { value: 'silt', label: '  (Silt)', description: ' ' },
      { value: 'red', label: '  (Red Soil)', description: '  ' }
    ]
  };

  const seasonOptions = {
    hindi: [
      { value: 'kharif', label: ' (-)' },
      { value: 'rabi', label: ' (-)' },
      { value: 'zaid', label: ' (-)' }
    ],
    english: [
      { value: 'kharif', label: 'Kharif (July-October)' },
      { value: 'rabi', label: 'Rabi (October-March)' },
      { value: 'zaid', label: 'Zaid (March-June)' }
    ],
    marathi: [
      { value: 'kharif', label: ' (-)' },
      { value: 'rabi', label: ' (-)' },
      { value: 'zaid', label: ' (-)' }
    ],
    gujarati: [
      { value: 'kharif', label: ' (-)' },
      { value: 'rabi', label: ' (-)' },
      { value: 'zaid', label: ' (-)' }
    ]
  };

  const irrigationOptions = {
    hindi: [
      { value: 'rainfed', label: ' ' },
      { value: 'irrigated', label: '' },
      { value: 'drip', label: ' ' },
      { value: 'sprinkler', label: '' }
    ],
    english: [
      { value: 'rainfed', label: 'Rain-fed' },
      { value: 'irrigated', label: 'Irrigated' },
      { value: 'drip', label: 'Drip Irrigation' },
      { value: 'sprinkler', label: 'Sprinkler' }
    ],
    marathi: [
      { value: 'rainfed', label: ' ' },
      { value: 'irrigated', label: '' },
      { value: 'drip', label: ' ' },
      { value: 'sprinkler', label: '' }
    ],
    gujarati: [
      { value: 'rainfed', label: ' ' },
      { value: 'irrigated', label: '' },
      { value: 'drip', label: ' ' },
      { value: 'sprinkler', label: '' }
    ]
  };

  const soilTypes = soilTypeOptions[language] || soilTypeOptions.hindi;
  const seasons = seasonOptions[language] || seasonOptions.hindi;
  const irrigationTypes = irrigationOptions[language] || irrigationOptions.hindi;

  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    
    try {
      // Store form data in localStorage for the recommendation result page
      const submissionData = {
        ...formData,
        timestamp: new Date().toISOString(),
        language: language
      };
      localStorage.setItem('cropRecommendationData', JSON.stringify(submissionData));
      
      // Simulate AI processing with realistic delay
      setTimeout(() => {
        setIsLoading(false);
        navigate('/recommendation-result');
      }, 2000);
    } catch (error) {
      console.error('Form submission error:', error);
      setIsLoading(false);
      const errorMessages = {
        hindi: '        ',
        english: 'Problem submitting form. Please try again.',
        marathi: '   .    .',
        gujarati: '   .     .'
      };
      alert(errorMessages[language] || errorMessages.hindi);
    }
  };

  const getCurrentLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setFormData(prev => ({
            ...prev,
            location: `${latitude.toFixed(6)}, ${longitude.toFixed(6)}`
          }));
        },
        (error) => {
          console.error('Location error:', error);
          const errorMessages = {
            hindi: '          ',
            english: 'Problem getting location. Please enter manually.',
            marathi: '  .    .',
            gujarati: '  .     .'
          };
          alert(errorMessages[language] || errorMessages.hindi);
        }
      );
    } else {
      const browserMessages = {
        hindi: '  GPS    ',
        english: 'Your browser does not support GPS.',
        marathi: '  GPS    .',
        gujarati: '  GPS    .'
      };
      alert(browserMessages[language] || browserMessages.hindi);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-4xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <button
                onClick={() => navigate('/dashboard')}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <ArrowLeft className="w-6 h-6 text-gray-600" />
              </button>
              <div>
                <h1 className="text-2xl font-bold text-gray-800">
                  {language === 'english' ? 'Crop Recommendation' : ' '}
                </h1>
                <p className="text-sm text-gray-600">
                  {language === 'english' ? 'Enter your farm information' : '     '}
                </p>
              </div>
            </div>
            <LanguageSwitcher />
          </div>
        </div>
      </div>

      <div className="max-w-2xl mx-auto px-4 py-6">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Farm Size */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Ruler className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Farm Size' : '  '}
            </label>
            <div className="grid grid-cols-2 gap-4">
              <input
                type="number"
                name="farmSize"
                value={formData.farmSize}
                onChange={handleInputChange}
                placeholder="5"
                className="input-field"
                required
              />
              <select className="input-field">
                <option value="acres">{language === 'english' ? 'Acres' : language === 'marathi' ? '' : language === 'gujarati' ? '' : ''}</option>
                <option value="hectares">{language === 'english' ? 'Hectares' : language === 'marathi' ? '' : language === 'gujarati' ? '' : ''}</option>
                <option value="bigha">{language === 'english' ? 'Bigha' : language === 'marathi' ? '' : language === 'gujarati' ? '' : ''}</option>
              </select>
            </div>
          </div>

          {/* Soil Type */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Droplets className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Soil Type' : '  '}
            </label>
            <select
              name="soilType"
              value={formData.soilType}
              onChange={handleInputChange}
              className="input-field"
              required
            >
              <option value="">{language === 'english' ? 'Select soil type' : language === 'marathi' ? '  ' : language === 'gujarati' ? '   ' : '   '}</option>
              {soilTypes.map((soil) => (
                <option key={soil.value} value={soil.value}>
                  {soil.label} - {soil.description}
                </option>
              ))}
            </select>
          </div>

          {/* Location */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <MapPin className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Location' : ''}
            </label>
            <div className="space-y-3">
              <div className="flex space-x-2">
                <input
                  type="text"
                  name="location"
                  value={formData.location}
                  onChange={handleInputChange}
                  placeholder={language === 'english' ? 'Village, District, State' : language === 'marathi' ? ', , ' : language === 'gujarati' ? ', , ' : ', , '}
                  className="input-field flex-1"
                  required
                />
                <button
                  type="button"
                  onClick={getCurrentLocation}
                  className="px-4 py-3 bg-blue-100 text-blue-700 rounded-lg hover:bg-blue-200 transition-colors"
                >
                  <Globe className="w-5 h-5" />
                </button>
              </div>
              <p className="text-xs text-gray-500">
                {language === 'english' ? 'Or press button to get GPS location' : language === 'marathi' ? ' GPS    ' : language === 'gujarati' ? ' GPS     ' : ' GPS       '}
              </p>
            </div>
          </div>

          {/* Previous Crop */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Wheat className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Previous Crop' : ' '}
            </label>
            <input
              type="text"
              name="previousCrop"
              value={formData.previousCrop}
              onChange={handleInputChange}
              placeholder=", , , "
              className="input-field"
            />
          </div>

          {/* Season */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Calendar className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Crop Season' : ' '}
            </label>
            <select
              name="season"
              value={formData.season}
              onChange={handleInputChange}
              className="input-field"
              required
            >
              <option value="">{language === 'english' ? 'Select season' : language === 'marathi' ? ' ' : language === 'gujarati' ? '  ' : ' '}</option>
              {seasons.map((season) => (
                <option key={season.value} value={season.value}>
                  {season.label}
                </option>
              ))}
            </select>
          </div>

          {/* Irrigation */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Droplets className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Irrigation Type' : '  '}
            </label>
            <select
              name="irrigation"
              value={formData.irrigation}
              onChange={handleInputChange}
              className="input-field"
              required
            >
              <option value="">{language === 'english' ? 'Select irrigation type' : language === 'marathi' ? '  ' : language === 'gujarati' ? '   ' : '  '}</option>
              {irrigationTypes.map((type) => (
                <option key={type.value} value={type.value}>
                  {type.label}
                </option>
              ))}
            </select>
          </div>

          {/* Budget Range */}
          <div className="card">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              <Smartphone className="inline w-4 h-4 mr-2" />
              {language === 'english' ? 'Budget Range (per acre)' : '  ( )'}
            </label>
            <select
              name="budget"
              value={formData.budget}
              onChange={handleInputChange}
              className="input-field"
            >
              <option value="">{language === 'english' ? 'Select budget' : language === 'marathi' ? ' ' : language === 'gujarati' ? '  ' : ' '}</option>
              <option value="low">{language === 'english' ? 'Low (5,000 - 15,000)' : language === 'marathi' ? ' (5,000 - 15,000)' : language === 'gujarati' ? ' (5,000 - 15,000)' : ' (5,000 - 15,000)'}</option>
              <option value="medium">{language === 'english' ? 'Medium (15,000 - 30,000)' : language === 'marathi' ? ' (15,000 - 30,000)' : language === 'gujarati' ? ' (15,000 - 30,000)' : ' (15,000 - 30,000)'}</option>
              <option value="high">{language === 'english' ? 'High (30,000+)' : language === 'marathi' ? ' (30,000+)' : language === 'gujarati' ? ' (30,000+)' : ' (30,000+)'}</option>
            </select>
          </div>

          {/* AI Integration Notice */}
          <div className="bg-yellow-50 border-2 border-yellow-200 rounded-xl p-6">
            <div className="flex items-center space-x-3 mb-3">
              <Zap className="w-6 h-6 text-yellow-600" />
              <h4 className="font-bold text-yellow-800">
                {language === 'english' ? 'AI Integration Port' : 
                 language === 'marathi' ? 'AI  ' :
                 language === 'gujarati' ? 'AI  ' : 'AI  '}
              </h4>
            </div>
            <p className="text-yellow-700 text-sm mb-3">
              {language === 'english' ? 
                <><strong>Backend Model Here:</strong> This form data will be sent to Gemini API which will provide crop recommendations based on soil, weather, and regional data.</> :
                language === 'marathi' ?
                <><strong>  :</strong>    Gemini API     ,         .</> :
                language === 'gujarati' ?
                <><strong>  :</strong>    Gemini API     ,         .</> :
                <><strong>  :</strong>    Gemini API     , ,          .</>
              }
            </p>
            <div className="bg-yellow-100 p-3 rounded-lg">
              <p className="text-yellow-800 text-xs">
                {language === 'english' ? (
                  <>
                    <strong>API Endpoint:</strong> POST /api/crop-recommendation<br/>
                    <strong>Model:</strong> Gemini Pro Vision + Custom ML Model<br/>
                    <strong>Input:</strong> Soil data, Weather data, Location, Season
                  </>
                ) : language === 'marathi' ? (
                  <>
                    <strong>API :</strong> POST /api/crop-recommendation<br/>
                    <strong>:</strong> Gemini Pro Vision + Custom ML Model<br/>
                    <strong>:</strong>  ,  , , 
                  </>
                ) : language === 'gujarati' ? (
                  <>
                    <strong>API :</strong> POST /api/crop-recommendation<br/>
                    <strong>:</strong> Gemini Pro Vision + Custom ML Model<br/>
                    <strong>:</strong>  ,  , , 
                  </>
                ) : (
                  <>
                    <strong>API :</strong> POST /api/crop-recommendation<br/>
                    <strong>:</strong> Gemini Pro Vision + Custom ML Model<br/>
                    <strong>:</strong> Soil data, Weather data, Location, Season
                  </>
                )}
              </p>
            </div>
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            disabled={isLoading}
            className={`w-full btn-primary text-lg py-4 ${
              isLoading ? 'opacity-50 cursor-not-allowed' : ''
            }`}
          >
            {isLoading ? (
              <div className="flex items-center justify-center space-x-2">
                <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                <span>
                  {language === 'english' ? 'Getting AI recommendations...' :
                   language === 'marathi' ? 'AI   ...' :
                   language === 'gujarati' ? 'AI    ...' : 'AI     ...'}
                </span>
              </div>
            ) : (
              <div className="flex items-center justify-center space-x-2">
                <Zap className="w-5 h-5" />
                <span>
                  {language === 'english' ? 'Get Recommendations (AI Powered)' :
                   language === 'marathi' ? '  (AI )' :
                   language === 'gujarati' ? '  (AI )' : '   (AI )'}
                </span>
              </div>
            )}
          </button>
        </form>
      </div>
    </div>
  );
};

export default CropRecommendationInput;
